import React, { Component } from 'react';
import { render } from 'react-dom';
import './index.css';

class Exchange extends Component {
  constructor(props) {
    super(props);
    this.state = { //state初期化
      isLoaded: false,
      weather: [],
      temp:[]
    };
  }
  
  componentDidMount() { //render直後に行いたい処理を書くところ
    fetch("http://api.openweathermap.org/data/2.5/weather?q=tokyo,jp&appid=01ad41bfbaa07921f515b5c89268ff8f&units=metric")
        .then(response => response.json())
        .then(json => {
            console.log(json.weather)
            console.log(json.main)
            this.setState({
                isLoaded: true,
                weather: json.weather,
                temp: json.main
            })
        }
           
        )
  }

  render() {
    let { weather, temp, isLoaded } = this.state;
    const now = "取得日時:" + new Date()
    if (!isLoaded) {
      return <div>...Loading</div>;
    } else {
        console.log(temp); 
        console.log(weather) 
      return (
        <div>
         <h1>東京の今の天気</h1>
         <p></p>
         <p>{now}</p>
        <table>
        <tr>
          <th>天気</th>
          <th>気温</th>
          <th>最低気温</th>
          <th>最高気温</th>
        </tr>
        <tr>
          <td>{weather[0]["main"]}</td>
          <td>{temp["temp"]}</td>
          <td>{temp["temp_min"]}</td>
          <td>{temp["temp_max"]}</td>
        </tr>
      </table>

    </div>

      );
    }
  }
}

export default Exchange;

render(<Exchange />, document.getElementById('root'));
